import React from 'react'
import { useRouteMatch } from 'react-router-dom'
import HallSpecificationsMainPage from './HallSpecificationsMainPage'
import HallSpecificationsPicturesPage from './HallSpecificationsPicturesPage'

const HallSpecificationsPage = () => {
  let match = useRouteMatch({ path: '/hall-specifications', exact: true })

  return match ? (
    <HallSpecificationsMainPage />
  ) : (
    <HallSpecificationsPicturesPage />
  )
}

export default HallSpecificationsPage
