import React, { useState, useEffect } from 'react'
import Row from 'react-bootstrap/Row'
import Col from 'react-bootstrap/Col'
import Form from 'react-bootstrap/Form'
import Masa7atiButton from '../components/Masa7atiButton'
import Table from 'react-bootstrap/Table'
import { Link } from 'react-router-dom'

const HallSpecificationsPicturesPage = () => {
  const [pictureName, setPictureName] = useState()
  const [pictureNames, setPictureNames] = useState([])
  const [canUpload, setCanUpload] = useState(false)

  useEffect(() => {
    setCanUpload(pictureNames.length >= 5)
  }, [pictureNames])
  return (
    <>
      <Row>
        <Col md={4}></Col>
        <Col md={'auto'}>
          <h1
            style={{
              borderStyle: 'solid',
              borderRadius: '10px',
            }}
            className='pr-5 pl-5'
          >
            Hall Specifications
          </h1>
        </Col>
      </Row>
      <Row className='mt-5'>
        <Col>
          <p>
            Please provide us with atleast <b>five</b> clear pictures for the
            hall
          </p>
        </Col>
      </Row>
      <Row className='mt-5'>
        <Col md={3}>
          <Form.File
            id='custom-file'
            label='Custom file input'
            custom
            onChange={(e) =>
              setPictureName(e.target.value.replace(/.*[/\\]/, ''))
            }
          />
        </Col>
        <Col md={1}>
          <Masa7atiButton
            onClick={() => setPictureNames([...pictureNames, pictureName])}
          >
            Add
          </Masa7atiButton>
        </Col>
        <Col md={1}>
          <Masa7atiButton
            onClick={() =>
              setPictureNames(
                pictureNames.filter((element) => element !== pictureName)
              )
            }
          >
            Remove
          </Masa7atiButton>
        </Col>
        <Col md={1}>
          <Link to='/submitted'>
            <Masa7atiButton disabled={!canUpload}>Upload</Masa7atiButton>
          </Link>
        </Col>
      </Row>
      <Row className='mt-5'>
        <Table striped bordered hover>
          <thead>
            <tr>
              <th>#</th>
              <th>Picture</th>
            </tr>
          </thead>
          <tbody>
            {pictureNames.map((element, index) => (
              <tr key={index}>
                <td>{index + 1}</td>
                <td>{element}</td>
              </tr>
            ))}
          </tbody>
        </Table>
      </Row>
    </>
  )
}

export default HallSpecificationsPicturesPage
