import React, { useState } from 'react'
import Row from 'react-bootstrap/Row'
import Col from 'react-bootstrap/Col'
import FormControl from 'react-bootstrap/FormControl'
import Masa7atiButton from '../components/Masa7atiButton'
import Table from 'react-bootstrap/Table'
import { Link, useLocation } from 'react-router-dom'

const HallSpecificationsMainPage = () => {
  let location = useLocation()
  const [hallName, setHallName] = useState('')
  const [hallNames, setHallNames] = useState([])
  return (
    <>
      <Row>
        <Col md={4}></Col>
        <Col md={'auto'}>
          <h1
            style={{
              borderStyle: 'solid',
              borderRadius: '10px',
            }}
            className='pr-5 pl-5'
          >
            Hall Specifications
          </h1>
        </Col>
      </Row>
      <Row className='mt-5'>
        <Col md={3}>
          <FormControl
            placeholder='Hall Name'
            onChange={(e) => setHallName(e.target.value)}
          />
        </Col>
        <Col md={1}>
          <Masa7atiButton
            onClick={() => setHallNames([...hallNames, hallName])}
          >
            Add
          </Masa7atiButton>
        </Col>
        <Col md={1}>
          <Masa7atiButton
            onClick={() =>
              setHallNames(hallNames.filter((element) => element !== hallName))
            }
          >
            Remove
          </Masa7atiButton>
        </Col>
      </Row>
      <Row className='mt-5'>
        <Table striped bordered hover>
          <thead>
            <tr>
              <th>#</th>
              <th>Hall Name</th>
              <th>Specifications</th>
            </tr>
          </thead>
          <tbody>
            {hallNames.map((element, index) => (
              <tr key={index}>
                <td>{index + 1}</td>
                <td>{element}</td>
                <td>
                  <Link to={`${location.pathname}/${index + 1}`}>
                    <Masa7atiButton>Show</Masa7atiButton>
                  </Link>
                </td>
              </tr>
            ))}
          </tbody>
        </Table>
      </Row>
    </>
  )
}

export default HallSpecificationsMainPage
